.tab-heading{
    list-style: none;
    padding:0;
    margin:0;
    width:100%;
}

.tab-heading li{
    padding: 15px 0;
    font-size: 18px;
    font-weight:500;
    color:#A6A5E4;
    width:75%;
    cursor: pointer;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    transition: all 0.5s ease-in-out;
}

.tab-heading li{
    border-bottom: 2px solid #A6A5E4;
}
.tab-heading li:last-child{
    border-bottom: none;
}
.active{
    transition: all 0.5s ease-in-out;
}
.active, .active:hover, .tab-heading li:hover{
    color:#fff !important; 
}
.tab-heading li.active::after{
    content: url('../../assets/images/arrow-right.png');
    position: absolute;
    right: -12px;
    background-color: #fff;
    width: 40px;
    height: 35px;
    border-top-left-radius: 20px;
    border-bottom-left-radius:20px;
    color: #000;
    text-align: center;
    line-height: 40px;
}